.App {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.App-header {
  display: grid;
  grid-template-columns: 80px auto 80px;

  width: 100%;
  height: 140px;

  overflow: hidden;

  &>button {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background-color: transparent;

    overflow: hidden;


    &>img {
      width: 120px;
      height: 120px;
      border: 2px solid rgba(65, 1, 65, 0.8);
      box-shadow: 0 0 2px 4px rgba(0, 0, 0, 0.4);
      border-radius: 50%;
    }

    &>svg {
      width: 60px;
      height: 60px;
    }
  }

  &>button::after {
    content: '';
    position: absolute;

    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);

    width: 0%;
    height: 0%;
    border-radius: 50%;
    background-color: aqua;

    transition: height 1s, width 1s, border-radius 2s;

    z-index: -1;
  }

  &>button:hover:after {
    width: 100%;
    height: 100%;
    border-radius: 0%;
  }
}